@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
body {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 5px;
}

* {
  margin: 0;
  padding: 0;
  background-color: #e1e9e5;
  font-family: poppins;
  box-sizing: border-box;
}

header {
  position: fixed;
  top: 0;
  z-index: 1000;
  display: flex;
  padding: 20px;
  justify-content: end;
  align-items: center;
  margin: 10px auto;
  margin-top: 0px;
  width: 100%;
}

.nav-bar {
  display: flex;
  gap: 10px;
  padding: 3px;
}

.menu-bar {
  display: none;
  padding: 5px;
}

.menu-bar .bar {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 3px 0;
  transition: transform 0.3s;
}

.cross .bar:nth-child(2) {
  display: none;
}

.cross .bar:nth-child(1) {
  transform: rotate(-45deg) translateY(6.5px);
}

.cross .bar:nth-child(3) {
  transform: rotate(45deg) translateY(-6.5px);
}

#logo {
  padding: 0 5px;
  margin-right: auto;
  display: flex;
  align-items: center; /* Align items vertically in the center */
  justify-content: center;
}

.nav-links {
  padding: 5px;
}

.nav-links > a {
  padding: 5px;
  text-decoration: none;
  color: black;
  margin: 0px 5px;
}

.nav-bar > a:hover,
.nav-bar > a.active {
  border-bottom: 2px solid black;
}

.sectionLink {
  padding: 3px;
  border-radius: 5px;
}

.sectionLink.active {
  background-color: #b3c8cf;
}

.content {
  padding-top: 50px;
  width: fit-content;
}

.content {
  padding-top: 50px;
  width: 100%;
}

#about {
  display: flex;
  padding: 10px;
  margin: 20px auto;
  justify-content: space-around;
}

#about > .intro {
  width: 700px;
}

.intro {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

#greeting {
  color: #0083a3;
}

#profession {
  color: #00008d;
}
.my-pic {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  mix-blend-mode: multiply;
  box-shadow: 2px 2px 2px grey;
}

#about-me {
  margin-top: auto;
  color: #00695c;
  text-align: justify;
}
.heading {
  color: #0083a3;
  padding: 10px;
  width: fit-content;
  margin: 5px 10px;
  border-bottom: 2px solid grey;
}

.education-container {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  justify-content: center;
  min-width: fit-content;
  margin: 10px auto;
}

.education-container > li {
  display: flex;
  padding: 10px;
  width: 300px;
  flex-direction: column;
  list-style: none;
  background-color: #f3f4e5;
  border-radius: 7px;
  margin: 10px 20px;
  box-shadow: 2px 2px 2px grey;
}

.education li * {
  margin: 0px 5px;
  background-color: #f3f4e5;
}

.education-container > li:hover {
  transform: scale(1.05);
}

.skills-container {
  list-style: none;
  padding: 10px;
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.skill {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  width: 100px;
}

.skills-container * {
  background-color: #f3f4e5;
}

.skill-name {
  margin: 5px auto;
  font-size: large;
}

.skill-icon {
  width: 75px;
  height: 75px;
}

.skill:hover {
  transform: scale(1.05);
}

.projects {
  width: 100%;
}

.projects-container {
  padding: 20px;
}

.project-item {
  padding: 10px;
  margin: 15px auto;
  width: 90%;
  text-decoration: none;
  background-color: #f3f4e5;
  color: black;
  border-radius: 10px;
}

.project-item * {
  background-color: #f3f4e5;
  margin: 10px auto;
}

.project-link {
  text-decoration: underline;
  color: #00008d;
}

.project-link {
  color: darkblue;
}

.note {
  padding: 10px;
}

.project-item:hover {
  transform: scale(1.05);
}

form {
  margin: 10px 0;
  padding: 20px;
  width: 400px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

input {
  padding: 7px;
  border-radius: 5px;
}

textarea {
  border-radius: 10px;
}

button[type="submit"] {
  width: fit-content;
  padding: 5px;
  font-size: medium;
  border-radius: 5px;
  background-color: #f3f4e5;
}

button[type="submit"]:hover {
  background-color: black;
  color: white;
}
@media (max-width: 450px) {
  .intro h1 {
    font-size: x-large;
    text-align: center;
  }

  #about {
    display: flex;
    padding: 10px;
    margin: 20px auto;
    justify-content: space-around;
    flex-wrap: wrap-reverse;
  }

  .my-pic {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    mix-blend-mode: multiply;
    box-shadow: 2px 2px 2px grey;
  }

  .nav-links {
    display: none; /* Hide navigation links by default on smaller screens */
    padding: 0;
  }

  .nav-links > a {
    margin: 0;
  }

  .menu-bar {
    display: flex; /* Display the hamburger icon */
    flex-direction: column;
    cursor: pointer;
  }

  .sectionLink {
    border-radius: 0;
  }

  /* Styling for the active (open) state of the navigation links */
  .nav-links.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100px;
    justify-content: space-around;
    top: 50px; /* Adjust as needed */
    right: 35px; /* Adjust as needed */
    background-color: white;
    border: 1px solid gray;
    z-index: 1000;
  }

  .nav-links > a {
    border-bottom: 2px solid black;
  }

  .education {
    padding: 10px;
    margin: 10px auto;
  }

  .education-container > li {
    display: flex;
    padding: 10px;
    width: 80%;
    flex-direction: column;
    list-style: none;
    background-color: #f3f4e5;
    border-radius: 7px;
    margin: 10px 20px;
    box-shadow: 2px 2px 2px grey;
  }

  .education-container li * {
    font-size: 15px;
  }

  .skill-icon {
    width: 40px;
    height: 40px;
  }

  .skill {
    width: 70px;
    height: 100px;
  }

  .project {
    width: 90%;
  }

  .project * {
    font-size: 15px;
  }

  form {
    margin: 10px 0;
    padding: 20px;
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}

@media (min-width: 450px) and (max-width: 700px) {
  .intro h1 {
    font-size: x-large;
    text-align: center;
  }

  #about {
    display: flex;
    padding: 10px;
    margin: 20px auto;
    justify-content: space-around;
    flex-wrap: wrap-reverse;
  }

  .my-pic {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    mix-blend-mode: multiply;
    box-shadow: 2px 2px 2px grey;
  }

  .nav-links {
    display: none; /* Hide navigation links by default on smaller screens */
    padding: 0;
  }

  .nav-links > a {
    margin: 0;
  }

  .sectionLink {
    border-radius: 0;
  }

  .menu-bar {
    display: flex; /* Display the hamburger icon */
    flex-direction: column;
    cursor: pointer;
  }

  /* Styling for the hamburger icon bars */
  .menu-bar .bar {
    width: 25px;
    height: 3px;
    background-color: black;
    margin: 3px 0;
  }

  /* Styling for the active (open) state of the navigation links */
  .nav-links.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100px;
    justify-content: space-around;
    top: 50px; /* Adjust as needed */
    right: 35px; /* Adjust as needed */
    background-color: white;
    border: 1px solid gray;
    z-index: 1000;
  }

  .nav-links > a {
    border-bottom: 2px solid black;
  }

  .skill-icon {
    width: 60px;
    height: 60px;
  }

  .skill {
    width: 90px;
  }
}

@media (min-width: 700px) and (max-width: 900px) {
  .my-pic {
    width: 180px;
    height: 180px;
  }
}
